import React, { useEffect, useRef } from 'react';
import './css/TechSection.css';
import frontend from './Images/Fornt-end.jpg';
import backend from './Images/backend.jpg';
import Ai from './Images/Ai.jpg'; 
import Database from './Images/database.jpg'; 

const TechSection = () => {
  const sectionsRef = useRef([]);

  const handleScroll = (index) => {
    const section = sectionsRef.current[index];
    if (section) {
      const { top, bottom } = section.getBoundingClientRect();
      section.classList.toggle('fadeInUp', top < window.innerHeight && bottom >= 0);
    }
  };

  useEffect(() => {
    const handleScrollEvent = () => {
      sectionsRef.current.forEach((_, index) => handleScroll(index));
    };

    window.addEventListener('scroll', handleScrollEvent);
    return () => window.removeEventListener('scroll', handleScrollEvent);
  }, []);

  return (
    <>
      {[{
        title: 'FRONT-END DEVELOPMENT',
        text: 'Websites that have a modern, user-friendly design are more engaging, appealing, and are an enjoyable experience instead of a frustrating hassle. Work on the most up-to-date UI frameworks like Bootstrap 5, CSS3, Tailwind CSS, Foundation, Materialize & Preprocessors like SCSS, LESS, and many more to create the best possible experience. Collaborate closely with backend developers, UI/UX designers, and project managers, to ensure that the frontend meets the clients requirements and is integrated with the backend logic.',
        img: frontend,
        reverse: false,
      }, {
        title: 'BACK-END DEVELOPMENT',
        text: 'Building robust back-end systems for a variety of web applications is the key to a stable and scalable platform. At Oksoft, get the opportunity to work with our experienced team who employ various techniques to ensure the quality and reliability of their code. As you join the backend development team, you will be supported by experienced developers who will help you learn and grow in your role. You will have access to training materials, documentation, and tools that will help you become proficient in backend development and deliver high-quality code. The team will also provide you with feedback and guidance to help you continuously improve your skills and become an integral part of the team.',
        img: backend,
        reverse: true,
      }, {
        title: 'DATABASE MANAGEMENT',
        text: 'Behind every great web application is a well-organized and secure database of information. Effective database management is essential for maintaining the integrity and reliability of data in modern software systems, and requires specialized skills and knowledge in data structures, database design, and security measures. Create, manage, and query your database using MySQL, PostgreSQL, MongoDB, and other technologies.',
        img: Database,
        reverse: false,
      }, {
        title: 'GENERATIVE AI',
        text: 'OkSoft is your reliable partner when it comes to harnessing the limitless potential of Generative AI for your web projects. Specializing in creating innovative tools, we seamlessly integrate cutting-edge AI models like ChatGPT and Google Bard to help you achieve your desired outcomes efficiently and effectively. Trust us to elevate your web development with the power of AI.',
        img: Ai,
        reverse: true,
      }].map((section, index) => (
        <div
          key={index}
          ref={el => sectionsRef.current[index] = el}
          className={`tech-section ${section.reverse ? 'reverse' : ''}`}
        >
          <div className="tech-text">
            <h1>{section.title}</h1>
            <p>{section.text}</p>
          </div>
          
          <div className="tech-logos1">
            <img src={section.img} alt={`${section.title} Logo`} />
          </div>
        </div>
      ))}
    </>
  );
};

export default TechSection;
